import React, { useEffect, useMemo, useRef, useState } from "react";
import { Modal, Upload, message, AutoComplete, Select, Form, Input } from "antd";
import { InboxOutlined } from '@ant-design/icons';
import CoverUpload from "@/components/CoverUpload";
import service from "@/service";
import { observer } from 'mobx-react'
import _ from 'lodash'
const { Dragger } = Upload;

type FileUploaderValue = {
    location: string;
    objectPath: string;
    size: number;
}
const FileUploader = observer((props: {
    value?: FileUploaderValue;
    onChange?: (value: FileUploaderValue) => void;
}) => (

    <Dragger
        name='file'
        multiple={false}
        accept={".apk,.ipa"}
        customRequest={async (options) => {
            // console.log('‰∏ä‰º†ÁöÑÊñá‰ª∂: ', options);
            const { onSuccess, onProgress } = options
            const file: any = options.file
            service.uploadFile(
                file,
                `${service.cosConfigs.COS_RES_DIR}/tmp/${file.name}`, //service.cosConfigs.COS_RES_DIR => /distribute/app
                (progress) => {
                    // console.log('‰∏ä‰º†ËøõÂ∫¶: ', progress);
                    if (onProgress) {
                        onProgress({ percent: progress.percent * 100 })
                    }
                },
                (err, data, opts) => {
                    // console.log('‰∏ä‰º†ÁªìÊùü: ', err, data, opts);
                    if (err == null) {
                        //‰∏ä‰º†Êñá‰ª∂ÂÆåÊàê„ÄÅÊãøÂà∞ Location Âíå key(objectPath)
                        let result = {
                            location: data.Location,
                            objectPath: opts.Key,
                            size: file.size
                        }
                        if (onSuccess) onSuccess(result)
                        if (props.onChange) props.onChange(result)
                    } else {
                        message.error(err.message)
                    }
                }
            )
        }}
    >
        <p className="ant-upload-drag-icon">
            <InboxOutlined />
        </p>
        <p className="ant-upload-text">ÁÇπÂáªÊàñÊãñÂä®Êñá‰ª∂Âà∞ËøôÈáåÊù•‰∏ä‰º†Â∫îÁî®Êñá‰ª∂</p>
        <p className="ant-upload-hint">
            ‰ªÖÊîØÊåÅ‰∏ä‰º† .apk Âíå .ipa Ê†ºÂºèÁöÑÂ∫îÁî®Êñá‰ª∂ÔºõËØ∑Âãø‰∏ä‰º†ÂÖ∂ÂÆÉÈùûÁõ∏ÂÖ≥Êñá‰ª∂Ôºõ
        </p>
    </Dragger >
));

function amendHttpPrefix(url: string) {
    if (!url.startsWith('http')) {
        return 'https://' + url
    }
    return url
}

const Publish = observer((props: {
    hide: () => void;
    visible: boolean;
    onPublishSuccess?: () => void;
}) => {
    const { hide, visible } = props
    const [form] = Form.useForm()
    const [apps, setApps] = useState<any[]>([])
    const bundleids = useMemo(() => apps.map((v, i) => ({ label: v.bundle_id, value: v.bundle_id })), [apps])
    const [selectedApp, setSelectedApp] = useState<any>()
    const bundleid = useRef('')
    const tmpFiles = useRef<any>({}).current
    const onFinish = (values: any) => {
        let ac = ((values.access_code ?? '') as string).trim()
        let formValue: any = {
            logo: amendHttpPrefix(values.logo.location),
            bundle_id: bundleid.current,
            ...(_.pick(values, ['name', 'version', 'build'])),
            size: values.app.size,
            access: ac == '' ? 'public' : 'private',
            access_code: ac
        }
        if (values.app.location.indexOf('.apk') != -1) {
            formValue.apk_url = amendHttpPrefix(values.app.location)
        } else {
            formValue.ipa_url = amendHttpPrefix(values.app.location)
        }
        if (selectedApp) {
            formValue.id = selectedApp.id
        }
        // console.log(formValue);
        let remove = message.loading("Ê≠£Âú®ÂèëÂ∏ÉÊõ¥Êñ∞‰∏≠„ÄÅËØ∑ÂãøÊìç‰Ωú...")
        service.publishApp(formValue)
            .then(rs => {
                remove()
                hide()
                if (rs.message) {
                    message.error(rs.message)
                } else {
                    message.success("ÂèëÂ∏ÉÊàêÂäü")
                    if (props.onPublishSuccess) props.onPublishSuccess()
                }
            })
            .catch(e => {
                hide()
                remove()
                message.error(e.message)
            })
    }
    const onBundleCompleteChange = (value: string, option: {
        label: string;
        value: string;
    } | {
        label: string;
        value: string;
    }[]) => {
        let foundApp;
        for (let a of apps) {
            if (a.bundle_id == value) {
                foundApp = a;
                break;
            }
        }
        bundleid.current = value
        if (foundApp) {
            form.setFieldsValue({
                name: foundApp.name,
                logo: { location: foundApp.logo, objectPath: '' },
            })
        }
    }
    useEffect(() => {
        service.appList().then(rs => {
            if (rs.message) {
                message.error(rs.message)
            } else {
                setApps(rs.data)
            }
        }).catch(e => {
            message.error(e.message)
        })
    }, [])
    return (
        <Modal
            visible={visible}
            width={500}
            centered
            okText="ÂèëÂ∏É"
            cancelText="ÂèñÊ∂à"
            bodyStyle={{
                padding: 0,
                margin: 0,
                height: '80vh',
            }}
            onOk={() => {
                form.submit()
            }}
            onCancel={() => {
                for (let k of Object.keys(tmpFiles)) {
                    let obj = tmpFiles[k]
                    if (obj) {
                        service.deleteFile(obj)
                    }
                }
                form.resetFields()
                hide()
            }}
        >
            <div style={{ height: '100%', width: '100%', overflow: 'scroll' }}>
                <Form
                    form={form}
                    name={'publish-app'}
                    onFinish={onFinish}
                    initialValues={{
                        build: 1
                    }}
                    onFieldsChange={(changed, all) => {
                        changed.map((v: any, i) => {
                            let n = v.name.toString()
                            if (n.indexOf('app') != -1) {
                                if ((v.value?.objectPath ?? '').length > 0) {
                                    tmpFiles[n] = v.value.objectPath
                                }
                            } else if (n.indexOf('logo') != -1) {
                                if ((v.value?.objectPath ?? '').length > 0) {
                                    tmpFiles[n] = v.value.objectPath
                                }
                            }
                        })
                    }}
                    labelCol={{ span: 4 }}
                    style={{ padding: '0 15px', paddingTop: 50 }}>
                    <Form.Item name={'app'} required>
                        <FileUploader />
                    </Form.Item>
                    <Form.Item name={'bundle_id'} label={'Â∫îÁî®ÂåÖÂêç'} required>
                        <Input.Group compact>
                            <AutoComplete
                                placeholder="Â∫îÁî®ÁöÑÂîØ‰∏ÄÊ†áËØÜ„ÄÅ‰æãÂ¶Ç: com.xxx"
                                options={bundleids}
                                onChange={onBundleCompleteChange}
                                style={{ width: '100%' }}
                            />
                        </Input.Group>
                    </Form.Item>
                    <Form.Item name={'name'} label={'Â∫îÁî®ÂêçÁß∞'} required>
                        <Input disabled={selectedApp?.name != undefined} />
                    </Form.Item>
                    <Form.Item name={'access_code'} label={'ËÆøÈóÆÂØÜÁ†Å'} tooltip="‰∏çËÆæÁΩÆÂàôÈªòËÆ§ÊâÄÊúâ‰∫∫ËÆøÈóÆ‰∏ãËΩΩÈìæÊé•ÂùáÂèØÁõ¥Êé•‰∏ãËΩΩ">
                        <Input placeholder="ËÆæÁΩÆApp‰∏ãËΩΩÂØÜÁ†Å" />
                    </Form.Item>
                    <Form.Item name={'logo'} label={'Â∫îÁî®ÂõæÊ†á'} required>
                        <CoverUpload />
                    </Form.Item>
                    <Form.Item name={'version'} label={'ÁâàÊú¨Âè∑'} required>
                        <Input placeholder="ÁâàÊú¨Âè∑„ÄÅ‰æãÂ¶Ç1.0.0" style={{ width: '50%' }} />
                    </Form.Item>
                    <Form.Item name={'build'} label={'ÁºñËØëÂè∑'} required>
                        <Input type={'number'} placeholder="ÂΩìÂâçÁâàÊú¨Á¨¨Âá†Ê¨°ÁºñËØë" style={{ width: '30%' }} />
                    </Form.Item>
                </Form>
            </div>
        </Modal>
    )
})
export default Publish